.bouquet-feedback-link {
  font-size: 0.95em;
  color: #888;
  cursor: pointer;
  margin-left: 0.5rem;
  padding-left: 0.2rem;
  transition: color 0.2s;
  text-decoration: none;
  align-self: flex-end;
  margin-bottom: 5px;
  vertical-align: middle;
  font-weight: bolder;
}
.bouquet-feedback-link:hover {
  color: #4d869c;
  text-decoration: none;
}

.bouquet-tambah-btn {
  margin: 1rem 0;
  padding: 0.5rem 1rem;
  background-color: #4caf50;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 6px;
  font-weight: bold;
  font-size: 1.05em;
  transition: background 0.18s;
}
.bouquet-tambah-btn:hover {
  background-color: #388e3c;
}

.bouquet-table {
  width: 100%;
  margin-top: 1rem;
  border-collapse: collapse;
  background: #fafbfc;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 2px 8px 0 rgba(77, 134, 156, 0.07);
}

.bouquet-table th,
.bouquet-table td {
  border: 1px solid #ccc;
  padding: 10px 8px;
  vertical-align: middle;
  font-size: 1em;
}

.bouquet-table thead {
  background-color: #f0f0f0;
}

.bouquet-logout-btn {
  margin-top: 1.5rem;
  padding: 0.5rem 1rem;
  background-color: #ff4d4d;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 6px;
  font-weight: bold;
  font-size: 1.05em;
  transition: background 0.18s;
}
.bouquet-logout-btn:hover {
  background-color: #d93636;
}

.bouquet-modal {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1000;
  justify-content: center;
  align-items: center;
}

.bouquet-modal-content {
  background: white;
  padding: 2rem;
  width: 100%;
  max-width: 400px;
  border-radius: 8px;
  position: relative;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

@media (max-width: 500px) {
  .bouquet-modal-content {
    width: 95vw;
    padding: 1rem;
  }
}

.bouquet-img-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.bouquet-preview-gambar {
  width: 64px;
  height: 64px;
  object-fit: cover;
  border-radius: 4px;
  display: block;
  margin: 0;
}

.bouquet-table-img {
  width: 64px;
  height: 64px;
  object-fit: cover;
  border-radius: 4px;
  display: block;
  margin: 0 auto;
}

.bouquet-input {
  width: 100%;
  border-radius: 4px;
  border: 1px solid #ccc;
  padding: 6px 8px;
  font-size: 1rem;
  margin-bottom: 0.5rem;
  box-sizing: border-box;
}

.bouquet-textarea {
  resize: vertical;
  min-height: 60px;
  max-width: 100%;
  min-width: 100%;
  max-height: 160px;
  font-family: inherit;
}

.bouquet-submit-btn {
  padding: 0.5rem 1rem;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: bold;
  margin-right: 0.5rem;
  font-size: 1em;
  transition: background 0.18s;
}
.bouquet-submit-btn:hover {
  background-color: #388e3c;
}

.bouquet-cancel-btn {
  padding: 0.5rem 1rem;
  background-color: #ccc;
  color: #333;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: bold;
  font-size: 1em;
  transition: background 0.18s;
}
.bouquet-cancel-btn:hover {
  background-color: #b0b0b0;
}

.bouquet-action-center {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
}

.bouquet-action-btn {
  padding: 0.3rem 0.8rem;
  border: none;
  border-radius: 5px;
  font-weight: bold;
  cursor: pointer;
  font-size: 0.95em;
  transition: background 0.2s;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 48px;
  min-height: 32px;
  line-height: 1.2;
}

.bouquet-edit-btn {
  background: #4d869c;
  color: #fff;
}
.bouquet-edit-btn:hover {
  background: #37647a;
}

.bouquet-delete-btn {
  background: #ff4d4d;
  color: #fff;
}
.bouquet-delete-btn:hover {
  background: #d93636;
}

.bouquet-search-row {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 0.5rem;
  margin-top: -60px;
  margin-bottom: 10px;
}

.bouquet-search-input {
  width: 250px;
  padding: 7px 12px;
  border: 1.5px solid #ccc;
  border-radius: 6px;
  font-size: 1em;
  margin-top: 10px;
  outline: none;
  transition: border 0.2s;
}
.bouquet-search-input:focus {
  border: 1.5px solid #4d869c;
}

.bouquet-search-btn {
  padding: 7px 18px;
  border-radius: 6px;
  border: none;
  background: #4d869c;
  color: #fff;
  font-weight: bold;
  font-size: 1em;
  margin-top: 10px;
  cursor: pointer;
  transition: background 0.2s;
}
.bouquet-search-btn:hover {
  background: #37647a;
}

.see-more {
  color: #4d869c;
  cursor: pointer;
  text-decoration: underline;
  font-size: 0.98em;
  margin-left: 4px;
}

/* Ensure .bouquet-deskripsi wraps text and prevents horizontal scroll/overflow */
.bouquet-deskripsi {
  display: block;
  white-space: pre-line;
  word-break: break-word;
  overflow-wrap: break-word;
  max-width: 250px;
  overflow-x: auto;
}

/* Prevent table cell overflow for deskripsi */
.bouquet-table td,
.bouquet-table th {
  vertical-align: top;
  word-break: break-word;
  white-space: normal;
  max-width: 260px;
  overflow-wrap: break-word;
}

.bouquet-file-preview-row {
  display: flex;
  align-items: center;
  gap: 12px;
}

/* Admin Feedback Table Styles */
.admin-feedback-table {
  width: 100%;
  margin-top: 1rem;
  border-collapse: collapse;
  background: #fafbfc;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 2px 8px 0 rgba(77, 134, 156, 0.07);
}

.admin-feedback-table th,
.admin-feedback-table td {
  border: 1px solid #ccc;
  padding: 10px 8px;
  vertical-align: middle;
  font-size: 1em;
}

.admin-feedback-table thead {
  background-color: #f0f0f0;
}

.admin-feedback-img-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.admin-feedback-table-img {
  width: 64px;
  height: 64px;
  object-fit: cover;
  border-radius: 4px;
  display: block;
  margin: 0 auto;
}

/* Prevent table header text from wrapping to new line */
.bouquet-table th,
.admin-feedback-table th {
  white-space: nowrap;
}

/* Prevent action button text from wrapping to new line */
.bouquet-action-btn,
.bouquet-edit-btn,
.bouquet-delete-btn {
  white-space: nowrap;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 48px;
  min-height: 32px;
  line-height: 1.2;
  padding: 0.3rem 0.8rem;
}

/* Optional: Prevent sort icon and th text from stacking vertically */
.bouquet-table th .th-flex,
.admin-feedback-table th .th-flex {
  flex-wrap: nowrap;
  gap: 6px;
}
