\r\n  font-weight: bold;\r\n  margin-bottom: 6px;\r\n  margin-top: 10px;\r\n}\r\n\r\n.login-input {\r\n  padding: 10px;\r\n  border: 1.4px solid #ccc;\r\n  border-radius: 10px;\r\n  margin-bottom: 10px;\r\n}\r\n\r\n.login-button {\r\n  background-color: #4d869c;\r\n  color: #fff;\r\n  padding: 10px;\r\n  border: none;\r\n  border-radius: 16px;\r\n  cursor: pointer;\r\n  margin-top: 10px;\r\n}\r\n\r\n.login-button:hover {\r\n  background-color: #3c6e83;\r\n}\r\n\r\n.login-redirect {\r\n  text-align: center;\r\n  margin-top: 15px;\r\n}\r\n\r\n.login-redirect a {\r\n  color: #00008b;\r\n  text-decoration: underline;\r\n}\r\n\r\n/* registrasi */\r\n.register-container {\r\n  max-width: 400px;\r\n  margin: 60px auto;\r\n  padding: 30px;\r\n  background-color: #f7f7f7;\r\n  border-radius: 16px;\r\n  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);\r\n}\r\n\r\n.register-title {\r\n  text-align: center;\r\n  color: #4d869c;\r\n  font-size: 24px;\r\n  font-weight: bold;\r\n  margin-bottom: 20px;\r\n}\r\n\r\n.register-form {\r\n  display: flex;\r\n  flex-direction: column;\r\n}\r\n\r\n.register-label {\r\n  color: #4d869c;\r\n  font-weight: bold;\r\n  margin-bottom: 6px;\r\n  margin-top: 10px;\r\n}\r\n\r\n.register-input {\r\n  padding: 10px;\r\n  border: 1.4px solid #ccc;\r\n  border-radius: 10px;\r\n  margin-bottom: 10px;\r\n}\r\n\r\n.register-button {\r\n  background-color: #4d869c;\r\n  color: #fff;\r\n  padding: 10px;\r\n  border: none;\r\n  border-radius: 16px;\r\n  cursor: pointer;\r\n  margin-top: 10px;\r\n}\r\n\r\n.register-button:hover {\r\n  background-color: #3c6e83;\r\n}\r\n\r\n.register-redirect {\r\n  text-align: center;\r\n  margin-top: 15px;\r\n}\r\n\r\n.register-redirect a {\r\n  color: #00008b;\r\n  text-decoration: underline;\r\n}\r\n\r\n/*\r\n    Footer\r\n  */\r\n\r\nfooter {\r\n  padding: 16px;\r\n}\r\n.footer-desc {\r\n  text-align: center;\r\n  margin: 10px 0;\r\n  font-size: 14px;\r\n  color: #858585;\r\n}\r\n\r\nfooter p {\r\n  text-align: center;\r\n  margin: 10px 0;\r\n  font-size: 14px;\r\n  color: #858585;\r\n}\r\n\r\nfooter p a {\r\n  color: #db0000;\r\n  text-decoration: none;\r\n  margin: 0 5px;\r\n  font-weight: bold;\r\n}\r\n\r\nfooter p a:hover {\r\n  color: #555555;\r\n  text-decoration: underline;\r\n}\r\n\r\n/* Feedback Page Styles */\r\n.feedback-page {\r\n  margin: 2rem auto;\r\n  max-width: 600px;\r\n  background: #fff;\r\n  border-radius: 18px;\r\n  box-shadow: 0 4px 24px 0 rgba(0, 0, 0, 0.08),\r\n    0 1.5px 4px 0 rgba(0, 0, 0, 0.04);\r\n  padding: 2.5rem 2rem 2rem 2rem;\r\n}\r\n\r\n.feedback-header {\r\n  margin-bottom: 1.5rem;\r\n}\r\n\r\n.feedback-title {\r\n  color: #222;\r\n  font-size: 2.1rem;\r\n  font-family: 'Poppins', sans-serif;\r\n  font-weight: 700;\r\n  letter-spacing: -1px;\r\n  margin-bottom: 0.5rem;\r\n}\r\n\r\n.feedback-form label {\r\n  display: block;\r\n  font-weight: 600;\r\n  color: #4d869c;\r\n  margin-bottom: 0.4rem;\r\n  margin-top: 1.2rem;\r\n  font-size: 1.05em;\r\n}\r\n\r\n.feedback-form input[type='file'] {\r\n  display: block;\r\n  width: 100%;\r\n  font-size: 1em;\r\n  padding: 0.5em 0.7em;\r\n  border: 1.5px solid #d3d3d3;\r\n  border-radius: 8px;\r\n  background: #fafbfc;\r\n  margin-bottom: 0.5rem;\r\n}\r\n\r\n#preview-gambar {\r\n  display: block;\r\n  margin: 0.5rem 0 0.8rem 0;\r\n  border-radius: 10px;\r\n  border: 1.5px solid #e0e0e0;\r\n  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.06);\r\n  max-width: 160px;\r\n  max-height: 120px;\r\n  object-fit: cover;\r\n}\r\n\r\n.feedback-form select,\r\n.feedback-form input[type='text'],\r\n.feedback-form textarea {\r\n  width: 100%;\r\n  padding: 0.7em 1em;\r\n  border: 1.5px solid #d3d3d3;\r\n  border-radius: 8px;\r\n  background: #fafbfc;\r\n  font-size: 1em;\r\n  margin-bottom: 0.2rem;\r\n  transition: border 0.2s;\r\n}\r\n\r\n.feedback-form select:focus,\r\n.feedback-form input[type='text']:focus,\r\n.feedback-form textarea:focus {\r\n  border: 1.5px solid #4d869c;\r\n  outline: none;\r\n}\r\n\r\n.feedback-form textarea